*{margin:0px}html,body{height:100vh;width:100vw;font-family:'Ubuntu', sans-serif}.background__main{align-items:center;background-image:url(/portfolio2.0/static/media/Coding-min.75deacac.jpg);display:flex;height:90vh;justify-content:center;width:100vw}.nav__main{align-items:center;background:#454b44;background:linear-gradient(180deg, #454b44 70%, #28536b 100%);display:flex;font-family:"Noto Sans JP",sans-serif;padding-top:20px;padding-bottom:20px}.nav__main .nav__fist-section{display:flex;width:50%;padding-bottom:20px;padding-top:5px}.nav__main .nav__fist-section .link{text-decoration:none;color:#f8fbef;padding-left:50px;font-size:25px}.nav__main .nav__fist-section .link i{padding-right:10px}.nav__main .nav__second-section{display:flex;align-items:center;width:15%;padding-bottom:20px;padding-top:5px;position:absolute;right:40px;justify-content:center}.nav__main .nav__second-section .link{text-decoration:none;color:#f8fbef;font-size:25px}.nav__main .nav__second-section .link i{padding-right:10px}.data__box{background-color:rgba(30,33,45,0.65);border-radius:360px;border-left:white solid 3px;border-right:white solid 3px;display:flex;justify-content:space-around;padding:30px;width:60%}.data__box .data__logo{border-radius:360px;border:10px solid #ffffff;color:#fde8e9;font-size:30px;padding:70px}.data__box .data__desc{align-items:center;color:#fde8e9;display:flex;font-family:"Ubuntu",sans-serif;font-size:40px}.contact__box{background-color:rgba(18,16,19,0.65);border-bottom:solid white 2px;border-top:solid white 2px;border-radius:90px;height:90%;width:80%;animation:fadeInAnimation ease 1.5s;animation-iteration-count:1;animation-fill-mode:forwards;font-family:"Noto Sans JP",sans-serif}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}.contact__box .contact__text{display:flex;align-items:center;justify-content:center;margin-top:20px;font-family:"Noto Sans JP",sans-serif;color:#f8fbef}.contact__box .contact__proffesional{display:flex;justify-content:space-evenly;height:40%;align-items:center}.contact__box .contact__others{display:flex;justify-content:space-evenly;height:40%;align-items:center}.flip-card__main{background-color:transparent;width:200px;height:200px;perspective:1000px;font-family:"Ubuntu",sans-serif}.flip-card__inner{position:relative;width:100%;height:100%;text-align:center;transition:transform 0.8s;transform-style:preserve-3d;text-decoration:none}.flip-card__main:hover .flip-card__inner{transform:rotateY(180deg)}.flip-card__main-pn:hover .flip-card__inner{transform:rotateY(180deg)}.flip-card__front{display:flex;align-items:center;justify-content:center;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;border-radius:30px;background-color:#bbb;color:black}.flip-card__back{display:flex;align-items:center;justify-content:center;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;border-radius:30px;background-color:dodgerblue;color:white;text-decoration:none;transform:rotateY(180deg)}.flip-card__back input{border:white 1px solid;font-family:"Noto Sans JP",sans-serif;width:80%;background-color:black;color:white;font-size:20px;cursor:pointer;border-radius:7px}.flip-card__back input:hover{animation:pulse 1s;animation-fill-mode:both}@keyframes pulse{0%{transform:scale3d(1, 1, 1)}50%{transform:scale3d(1.05, 1.05, 1.05)}100%{transform:scale3d(1, 1, 1)}}

